.header-btn_wrapper {
    position: absolute;
    top: 50%;
    right: 15px;
    transform: translateY(-50%);
}

.header__btn {
    position: relative;
}

.header__num {
    width: 17px;
    height: 17px;
    border-radius: 50%;
    background: rgba(#e9e9e9, .9);

    color: #900;
    font-size: 12px;
    line-height: 16px;

    position: absolute;
    top: 50%;
    right: -50%;
    transform: translate3d(-50%, -50%, 0)
}