.cart-item {
    margin-top: 20px;
}

.cart-item__image {
    width: 50px;
}

.cart-item__block {
    width: 100%;
    padding: 10px 25px;

    font-size: 16px;

    border: 1px solid #000;
    border-radius: 5px;

    @media screen and (max-width: 768px) {
        font-size: 10px;
        padding: 10px 15px;
    }
}